/* src/app/tooling/components/control3-d/control.page.scss */
.main-form {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
}
.main-form .left-side {
  max-width: 30%;
}
.main-form > * {
  width: auto;
  height: auto;
}
.flex-row {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.print-preview {
  overflow-y: auto;
}
.vertical {
  display: flex;
  flex-direction: column;
}
.vertical *[contentEditable=true]:focus,
.vertical *[contentEditable=true]:hover {
  outline: 2px solid #1976d2;
}
